import { Component, OnInit, Inject, Output, EventEmitter } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Router } from '@angular/router';
import { AppConstants } from 'src/app/shared/config/app.constant';
import { ApiService } from 'src/app/shared/services/api.service';
import Swal from 'sweetalert2';




@Component({
  selector: 'app-value-date-error',
  templateUrl: './value-date-error.component.html',
  styleUrls: ['./value-date-error.component.css']
})

export class ValueDateErrorComponent implements OnInit {
  proceedDuplicates=false;
  user_id = sessionStorage.getItem('user_id');
 
  constructor( private api:ApiService,public dialogRef: MatDialogRef<ValueDateErrorComponent>,private router:Router,
   
    @Inject(MAT_DIALOG_DATA) public data: any
   ) { }

  ngOnInit() {
    console.log("errormessage",this.data)
  }
  
  onProceed()
  {
    this.data.excelDataProcessingObj.proceedDuplicates=true;
    console.log(this.data.excelDataProcessingObj);
    this.api.showDetailServiceInExcelDataProcessing(this.data.excelDataProcessingObj,this.user_id).subscribe(proceedResp=>
      {
        console.log(proceedResp);
       
     
      })
   
  }
}
